import React from 'react';
import Button from '@material-ui/core/Button';
import AddIcon from '@material-ui/icons/Add';
import { useState } from 'react';

const CreateNote = (props)=>{

    const [expand ,setExpand] = useState(false);

    const [note, setNote] =useState({
        title: "",
        content: "",
    })

    const inputEvent = (event) =>{
        const {name, value} = event.target;
        setNote((prevData) =>{
            return{
                ...prevData,
                [name] : value,
            }
        })
    }

    const addEvent = () =>{
        props.passNote(note);
        setNote({
            title: "",
            content: "",
        })
    }

    const expandIt = () =>{
        setExpand(true);
    }
    const backToNormal = () =>{
        setExpand(false);
    }   

    return(
        <>
            <div className="main_note" onDoubleClick={backToNormal}>
                <form>
                    {expand?
                    <input type="text" name="title" value={note.title} onChange={inputEvent} placeholder="Title" autoComplete="off" /> : null}

                    <textarea rows='' cols="" name="content" value={note.content} onChange={inputEvent} placeholder="Write Note..." onClick={expandIt}/>

                    {expand?
                    <Button onClick={addEvent}>
                        <AddIcon className="plus_sign"/>
                    </Button> : null}
                </form>
            </div>
        </>
    )

}
export default CreateNote;